<template>
  <v-container>
    <v-form ref="loginForm" v-model="valid" @submit.prevent="handleSubmit()">
      <v-card width="500" class="mx-auto pt-5">
        <v-card-title class="white--text pa-5 card-title">
          <v-icon color="white">fa-user</v-icon>
          <p class="ml-3 mb-0 ">
            Entrar
          </p>
        </v-card-title>
        <v-card-text class="pt-8 px-8">
          <v-text-field
            label="CPF"
            v-model="credentials.cpf"
            v-mask="'###########'"
            :hint="'Somente números'"
            inputmode="numeric"
            clearable
            outlined
            :rules="validators.cpf"
            class="mb-4"
          ></v-text-field>
          <v-text-field
            label="Senha"
            v-model="credentials.password"
            clearable
            outlined
            :rules="validators.password"
            type="password"
          ></v-text-field>
        </v-card-text>
        <v-card-actions class="flex-column justify-center">
          <v-btn class="white--text btn mb-4" elevation="2" type="submit">
            Logar
          </v-btn>
          <div class="message">
            <p>{{ message }}</p>
          </div>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-container>
</template>

<script>
  import { Validator as v } from '../helpers/validators';

  export default {
    name: 'Login',
    data() {
      return {
        valid: false,
        message: '',
        credentials: {
          cpf: '',
          password: '',
        },
        validators: {
          cpf: [v.required(), v.len(11)],
          password: [v.required()],
        },
      };
    },
    methods: {
      handleSubmit() {
        this.$refs.loginForm.validate();
        if (!this.valid) {
          return (this.message = 'Por favor, preencha os dados corretamente!');
        }
        this.message = 'Verificando credenciais...';
        this.$store
          .dispatch('login', this.credentials)
          .then(data => {
            if (data.data.successful_login) {
              this.$router.push({ name: 'home' });
            } else {
              this.message = 'Credenciais inválidas!';
            }
          })
          .catch(() => {
            this.message = 'Não foi possível se conectar com o servidor :(';
          });
      },
    },
  };
</script>

<style scoped>
  .message p {
    color: #aa4465;
    font-size: 1.1rem;
    margin-top: 5px;
  }
  .btn {
    background-color: #af7b48e1 !important;
  }
  .card-title {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #aa4465;
  }
</style>
